import React, { useEffect, useState } from 'react';
import Main from '../components/section/Main';
import { useSelector } from 'react-redux';
import { getCurrentTime, getDayType } from '../data/time';
import { PiSubwayFill } from "react-icons/pi";
import train from '../assets/img/nav/train.png';
import wait from '../assets/img/nav/waiting.png';
import walk from '../assets/img/nav/walk.png';

const Nav = () => {
    const startStation = useSelector(state => state.startStation);
    const endStation = useSelector(state => state.endStation);

    const [currentTime, setCurrentTime] = useState(getCurrentTime());
    const [dayType, setDayType] = useState(getDayType());

    const [cuTime, setCuTime] = useState("12:30:15");
    const [fiTime, setFiTime] = useState("12:33:00");
    const [path, setPath] = useState([]);
    const [startLine, setStartLine] = useState("");
    const [eachTime, setEachTime] = useState([]);
    const [eachWalkingTime, setEachWalkingTime] = useState([]);
    const [eachTransferStation, setEachTransferStation] = useState([]);
    const [eachTypeOfLine, setEachTypeOfLine] = useState([]);
    const [eachWaitingTime, setEachWaitingTime] = useState([]);
    const [TotalTime, setTotalTime] = useState(0);
    const [estimatedArrival, setFinalTime] = useState("");

    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(getCurrentTime());
        }, 1000);

        return () => clearInterval(timer);
    }, []);

    const fetchDataFromSpring = async () => {
        try {
            const url = `http://localhost:8080/SearchRoute?start=${encodeURIComponent(startStation)}&end=${encodeURIComponent(endStation)}`;
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
            });

            const data = await response.json();
            setCuTime(data.cuTime);
            setFiTime(data.fiTime);
            setPath(data.path);
            setStartLine(data.startLine);
            setEachTime(data.eachTime);
            setEachWalkingTime(data.eachWalkingTime);
            setEachTransferStation(data.eachTransferStation);
            setEachTypeOfLine(data.eachTypeOfLine);
            setEachWaitingTime(data.eachWaitingTime);
            setTotalTime(data.totalTime);
            setFinalTime(data.finalTime);
        } catch (error) {
            console.error('데이터 가져오기 실패:', error);
        }
    };

    useEffect(() => {
        if (startStation && endStation) {
            fetchDataFromSpring();
        }
    }, [startStation, endStation]);

    const getLineColor = (line) => {
        // 지하철 노선별 색상 매핑
        const lineColors = {
            '1호선': '#0d3692',
            '2호선': '#33a23d',
            '3호선': '#fe5d10',
            '4호선': '#00a2d1',
            '5호선': '#8b50a4',
            '6호선': '#c55c1d',
            '7호선': '#54640d',
            '8호선': '#f14c82',
        };
        return lineColors[line] || '#000'; // 기본 색상은 검정색
    };

    // 비율 계산
    const totalTransferTime = eachWalkingTime.length > 0 ? eachWalkingTime.reduce((acc, cur) => acc + cur, 0) : 0;
    const totalWaitingTime = eachWaitingTime.length > 0 ? eachWaitingTime.reduce((acc, cur) => acc + cur, 0) : 0;
    const totalGoTime = eachTime.length > 0 ? eachTime.reduce((acc, cur) => acc + cur, 0) : 0;
    const totalTime = totalTransferTime + totalWaitingTime + totalGoTime;
    const ratio = 1000 / totalTime;

    const renderTransferBars = () => {
        const transferBars = [];
        const iconSize = 20;

        for (let i = 0; i < eachTime.length; i++) {
            const goBarWidth = eachTime[i] * ratio;
            const transferBarWidth = eachWalkingTime[i] * ratio;
            const waitingBarWidth = eachWaitingTime[i] * ratio;

            transferBars.push(
                <div key={`go${i}`} style={{ display: 'inline-block', width: `${goBarWidth}px`, height: '30px', backgroundColor: getLineColor(i === 0 ? startLine : eachTypeOfLine[i - 1]), position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={train} alt="subway" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${goBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: getLineColor(i === 0 ? startLine : eachTypeOfLine[i - 1]) }}>{eachTime[i]}분-{(i === 0 ? startStation : eachTransferStation[i - 1])}({(i === 0 ? startLine : eachTypeOfLine[i - 1])})</span>
                </div>
            );
            transferBars.push(
                <div key={`transfer${i}`} style={{ display: 'inline-block', width: `${transferBarWidth}px`, height: '30px', backgroundColor: 'lightgray', position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={walk} alt="walk" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${transferBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: '#000' }}>{eachWalkingTime[i]}분</span>
                </div>
            );
            transferBars.push(
                <div key={`wait${i}`} style={{ display: 'inline-block', width: `${waitingBarWidth}px`, height: '30px', backgroundColor: 'red', position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={wait} alt="wait" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${waitingBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: '#000' }}>{eachWaitingTime[i]}분</span>
                </div>
            );

        }

        const lastGoBarWidth = eachTime[eachTime.length - 1] * ratio;

        transferBars.push(
            <div key={`go${eachTime.length - 1}`} style={{ display: 'inline-block', width: `${lastGoBarWidth}px`, height: '30px', backgroundColor: getLineColor(eachTypeOfLine[eachTypeOfLine.length - 1]), position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                <img src={train} alt="subway" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${lastGoBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: getLineColor(eachTypeOfLine[eachTypeOfLine.length - 1]) }}>{eachTime[eachTime.length - 1]}분-{(eachTransferStation[eachTransferStation.length - 1])}({eachTypeOfLine[eachTypeOfLine.length - 1]})</span>
            </div>
        );

        return transferBars;
    };

    const renderRoutes = (reconstructedData) => {
        return (
            <ul>
                {reconstructedData.map((route, index) => (
                    <li key={index} style={{marginBottom: '20px'}}>
                        {renderTransferBars(route)}
                    </li>
                ))}
            </ul>
        );
    };
};

export default Nav;

